
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}


html,body {
    height: 100%;
    margin: 0;
    font-family: "Microsoft YaHei",Arial,Helvetica,sans-serif;
}
.wrap {
    width: 1200px;
    margin:0 auto;
    background-image: url(data:image/png;base64,);
    background-size: 30px 30px;
    min-height: 100%;
    -webkit-box-sizing:border-box;
    box-sizing: border-box;
    padding:20px 40px;
}
h1 {
    text-align: center;
}
.catalog .item {
    float: left;
    width: 200px;
    height: 46px;
    margin: 10px 15px;
    text-align: center;
}
.catalog .item a {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 35px;
    text-decoration: none;
    cursor: pointer;
    display: block;
    background-color: #fff;
    border:1px solid #ccc;
    border-radius: 6px;
    font-size: 12px;
    padding:5px 12px;
    color: rgb(101, 123, 131);
    -webkit-box-shadow: 0 1px 2px rgba(0,0,0,.1);
            box-shadow: 0 1px 2px rgba(0,0,0,.1);
    padding-left: 72px;
    overflow: hidden;
}
.catalog .item a:hover {
    background-color: #e6e6e6;
    border-color: #adadad;
}
.catalog .item a .date {
    position: absolute;
    left: 0;
    top: 0;
    width: 67px;
    height: 100%;
    background-color: #444;
    color: #fff;
    text-align: center;
}
.catalog .item a .date span {
    display: inline-block;
    vertical-align: middle;
}
.catalog .item a .date:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 100%;
    vertical-align: middle;
}

.page-login form {
  width: 400px;
  margin: 0 auto;
}
.form-group {
  margin-bottom: 15px;
  position: relative;
}
.form-group .form-control,
.form-group button {
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  outline: none;
  height: 30px;
  width: 100%;
  padding: 5px;
}
.form-group button {
  outline: none;
  border: 1px solid #41b883;
  background-color: #41b883;
  width: auto;
  color: #fff;
}









































































